import React from 'react';
import { useTranslation } from 'react-i18next';
import styled from 'styled-components';
import { StyledButton } from '../../common/components';
import { themeColor } from '../../common/theming';
import { Popup } from '../components';
import Close from '../icons/close';
import { MessagePopupsProps } from './types';

const Content = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  justify-content: space-between;
  & > p {
    margin: 0;
    font-weight: bold;
  }
`;

const AppButton = styled(StyledButton)`
  width: fit-content;
  margin: 0 10px;
  height: 35px;
  border: 1px solid ${themeColor('neutralDark')};
  background: none;
  color: ${themeColor('neutralDark')};
  white-space: nowrap;
`;

const CloseButton = styled.button`
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  height: 20px;
  width: 20px;
  justify-self: right;
  align-self: center;
`;

const BalanceChanged: React.FC<MessagePopupsProps> = ({ isO2, ...props }) => {
  const { t } = useTranslation();

  const onClick = () => {
    if (props.onClose) {
      props.onClose();
    }

    browser.runtime.sendMessage({
      type: 'openApp',
    });
  };

  return (
    <Popup {...props} showLogo={false} showClose={false}>
      <Content>
        <p>
          {isO2
            ? t('contentApp.deals.balanceChanged.o2')
            : t('contentApp.deals.balanceChanged.received')}
        </p>
        <AppButton onClick={onClick} className="outlined">
          {t('contentApp.deals.balanceChanged.goToApp')}
        </AppButton>
        <CloseButton onClick={props.onClose}>
          <Close />
        </CloseButton>
      </Content>
    </Popup>
  );
};

export default BalanceChanged;
