export enum CashbackType {
  percentage = 'Percentage',
  fixed = 'Fixed',
}

export interface CouponCode {
  code: string;
  summary: string;
}

export interface DealSummary {
  id: string;
  storeId: string;
  storeName: string;
  cashback: number;
  codes: CouponCode[];
  hasSale: boolean;
  logo: string;
  cashbackType: CashbackType;
  currency: string | null;
  earthmarkRating?: number | null; // earth mark rating out of 5
  // affiliateAction: AffiliateAction;
}

export interface DealSummaryWithDomains extends DealSummary {
  domains: string[];
}

export interface DealClickResponse {
  link: string | null;
}

export interface Balance {
  pending: number;
  available: number;
  allTimeEarnings: number;
  allTimeDonations: number;
  currency: 'GBP' | 'USD' | 'EUR' | string;
}
