import React from 'react';
import styled from 'styled-components';
import { boxShadow, rootStyles, themeColor } from '../../common/theming';
import PopupLogo from './PopupLogo';
import CloseButton from './CloseButton';

const Container = styled.div`
  position: fixed;
  bottom: 5px;
  z-index: 2147483647;
  padding: 12px;
  overflow: auto;
  border-radius: 4px;
  width: 90%;
  max-width: 380px;
  transform: translateX(-50%);
  left: 50%;
  box-shadow: ${boxShadow({
    offset: { x: 0, y: 2 },
    blur: 8,
    spread: 2,
    opacity: 0.3,
  })};
  color: ${themeColor('neutralDark')};
  background: ${themeColor('neutralLighter')};
  ${rootStyles};
  display: grid;
  grid-template-columns: 30px auto 30px;
  grid-row-gap: 10px;
`;

const Content = styled.div`
  display: flex;
  overflow: auto;
  grid-area: 2 / 1 / 3 / 4;
`;

const Title = styled.p`
  font-size: 12px;
  font-weight: bold;
  grid-area: 1 / 2 / 2 / 3;
  text-align: center;
  margin: 0;
  align-self: center;
`;

export interface PopupProps {
  showClose?: boolean;
  showLogo?: boolean;
  onClose?(): void;
  title?: string | React.ReactNode;
}

const Popup: React.FC<PopupProps> = ({
  showClose = true,
  showLogo = true,
  title,
  onClose,
  children,
}) => {
  return (
    <Container>
      {showLogo && <PopupLogo />}
      {title && <Title>{title}</Title>}
      {showClose && <CloseButton onClick={onClose} />}
      <Content>{children}</Content>
    </Container>
  );
};

export default Popup;
