import React, { useEffect, useState } from 'react';
import { ContentScriptMessage } from '../common/messageTypes/contentScriptMessage';
import env from '../env';
import MessageContent from './MessageContent';

const MessageManager: React.FC = () => {
  const [selectedMessage, setSelectedMessage] =
    useState<ContentScriptMessage | null>(null);

  useEffect(() => {
    const handler = async (handledMessage: object) => {
      console.log('got a new message ', handledMessage);
      const newMessage = handledMessage as ContentScriptMessage;

      if (newMessage) {
        setSelectedMessage(newMessage);
      }
    };

    browser.runtime.onMessage.addListener(handler);

    return () => browser.runtime.onMessage.removeListener(handler);
  }, []);

  const onClose = () => {
    setSelectedMessage(null);
  };

  return selectedMessage ? (
    <MessageContent
      message={selectedMessage}
      onClose={onClose}
      isO2={env.REACT_APP_IS_O2}
    />
  ) : null;
};

export default MessageManager;
