import clsx from 'clsx';
import React from 'react';
import styled from 'styled-components';
import { Spinner } from './common';

const Button = styled.button`
  height: 44px;
  background-color: ${(props) => props.color ?? '#363636'};
  color: #ffffff;
  text-align: center;
  font-weight: 700;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border: none;
  outline: none;

  & .spinner {
    margin-right: -20px;
    &.hidden {
      display: none;
    }
  }
`;

interface Props extends React.ButtonHTMLAttributes<HTMLButtonElement> {
  loading?: boolean;
  loadingColor?: string;
}

const StyledButton: React.FC<Props> = ({
  loading = false,
  onClick,
  className,
  loadingColor,
  children,
  ...props
}) => {
  return (
    <Button
      onClick={!props.disabled ? onClick : undefined}
      className={className}
      {...props}
    >
      {children}
      <Spinner
        className={clsx({
          hidden: !loading,
          spinner: true,
        })}
        color={loadingColor}
      />
    </Button>
  );
};

export default StyledButton;
