import clsx from 'clsx';
import React, { useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import styled from 'styled-components';
import { CashbackType } from '../../api/types';
import { useCashback, useCoupons, useEarthmarkScore } from '../../common/deal';
import { DealActivatedMessage } from '../../common/messageTypes/contentScriptMessage';
import { themeColor } from '../../common/theming';
import { CloseButton, Earthmark, Popup } from '../components';
import { useTimeout } from './helpers';
import { BaseMessagePopupProps } from './types';

const Content = styled.div`
  width: 100%;

  &.inline {
    display: grid;
    grid-template-columns: min-content auto 30px;
    align-items: center;
  }
`;

const Text = styled.p`
  padding: 0;
  margin: 0;
  color: ${themeColor('success')};
  text-align: center;
  font-weight: 700;
  width: 100%;

  &.outlined {
    border: 1px solid ${themeColor('success')};
    box-shadow: unset;
    padding: 10px;
  }
`;

const DealActivated: React.FC<BaseMessagePopupProps & DealActivatedMessage> = ({
  deal,
  isO2,
  ...props
}) => {
  const { t } = useTranslation();
  const cashback = useCashback(deal);
  const { hasCoupons } = useCoupons(deal);

  const earthmarkScore = useEarthmarkScore(deal);

  const onDismiss = () => {
    if (props.onClose) {
      props.onClose();
    }
  };

  useTimeout(3000, onDismiss);

  const label = useMemo(() => {
    if (
      !isO2 &&
      ((deal.cashbackType === CashbackType.fixed && deal.currency) ||
        deal.cashback > 0)
    ) {
      return hasCoupons
        ? t('contentApp.deals.activated.cashbackWithCoupons', {
            cashback,
          })
        : t('contentApp.deals.activated.cashback', {
            cashback,
          });
    }

    return hasCoupons
      ? t('contentApp.deals.activated.basicWithCoupons')
      : t('contentApp.deals.activated.basic');
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [deal, hasCoupons, cashback]);

  return (
    <Popup
      {...props}
      showClose={!isO2}
      showLogo={!isO2}
      onClose={onDismiss}
      title={<></>}
    >
      <Content
        className={clsx({
          inline: isO2,
        })}
      >
        {isO2 && <Earthmark score={earthmarkScore} />}
        <Text
          className={clsx({
            outlined: !isO2,
          })}
        >
          {label}
        </Text>
        {isO2 && <CloseButton onClick={onDismiss} />}
      </Content>
    </Popup>
  );
};

export default DealActivated;
