import React, { useState, useEffect } from 'react';
import styled, { keyframes } from 'styled-components';
import { themeColor } from '../theming';

const animation = keyframes`
  0% {
    transform: scale(0.95);
  }
  5% {
    transform: scale(1.1);
  }
  39% {
    transform: scale(0.85);
  }
  45% {
    transform: scale(1);
  }
  60% {
    transform: scale(0.95);
  }
  100% {
    transform: scale(0.9);
  }
`;

const Container = styled.div`
  width: 100%;
  height: 100%;
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  & > div {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
    transform: rotate(45deg);
    transform-origin: 40px 40px;
    & div {
      top: 32px;
      left: 32px;
      position: absolute;
      width: 32px;
      height: 32px;
      background: ${themeColor('primary')};
      animation: ${animation} 1.2s infinite cubic-bezier(0.215, 0.61, 0.355, 1);
    }
    & div:after,
    & div:before {
      content: ' ';
      position: absolute;
      display: block;
      width: 32px;
      height: 32px;
      background: ${themeColor('primary')};
    }
    & div:before {
      left: -24px;
      border-radius: 50% 0 0 50%;
    }
    & div:after {
      top: -24px;
      border-radius: 50% 50% 0 0;
    }
  }
`;

interface CoreLoadingIndicatorProps {
  show: boolean;
}

const HeartLoadingIndicator: React.FC<CoreLoadingIndicatorProps> = ({
  show,
}) => {
  if (!show) {
    return null;
  }
  return (
    <Container>
      <div>
        <div></div>
      </div>
    </Container>
  );
};

interface LoadingIndicatorProps {
  delay?: number;
}

const LoadingIndicator: React.FC<LoadingIndicatorProps> = ({ delay = 200 }) => {
  const [show, setShow] = useState(delay === 0);

  useEffect(() => {
    if (delay === 0) {
      return;
    }

    const timeout = setTimeout(() => {
      setShow(true);
    }, delay);

    return () => clearTimeout(timeout);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return <HeartLoadingIndicator show={show} />;
};

export default LoadingIndicator;
