import React, { useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import styled from 'styled-components';
import { setInteraction } from '../../api/services/deals';
import { writeToClipboard } from '../../common/clipboard';
import { StyledButton } from '../../common/components';
import { useCashback, useCoupons, useEarthmarkScore } from '../../common/deal';
import { getDomainFromUrl } from '../../common/helpers';
import { DealsAvailableMessage } from '../../common/messageTypes/contentScriptMessage';
import { colors, themeColor } from '../../common/theming';
import { DealButton, Earthmark, Popup } from '../components';
import { BaseMessagePopupProps } from './types';

const Content = styled.div`
  width: 100%;
`;

const NoStyleButton = styled(StyledButton)`
  background: none;
  border: none;
  color: ${themeColor('neutralDark')};
  margin: 0;
  margin-top: 5px;
`;

const DealsAvailable: React.FC<
  BaseMessagePopupProps & DealsAvailableMessage
> = ({ deal, isO2, ...props }) => {
  const { t } = useTranslation();

  const cashback = useCashback(deal);
  const { codes, hasCoupons } = useCoupons(deal);

  const earthmarkScore = useEarthmarkScore(deal);

  const cashbackLabel = useMemo(() => {
    return t('contentApp.deals.available.activateAll', { cashback });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [cashback]);

  const copyCode = () => {
    if (hasCoupons) {
      writeToClipboard(codes[0].code);
    }
  };

  const onActivateDeal = () => {
    copyCode();

    browser.runtime.sendMessage({
      type: 'openDeal',
      deal,
      sameTab: true,
    });
  };

  const onActivateAll = () => {
    copyCode();

    browser.runtime.sendMessage({
      type: 'activateAllDeals',
    });
    onActivateDeal();
  };

  const onDismiss = async () => {
    const domain = getDomainFromUrl(window.location.href);
    await setInteraction(domain, { type: 'dismiss' });

    if (props.onClose) {
      props.onClose();
    }
  };

  return (
    <Popup
      {...props}
      showClose={true}
      showLogo={true}
      onClose={onDismiss}
      title={
        isO2 ? (
          <Earthmark score={earthmarkScore} />
        ) : (
          t('contentApp.deals.available.title')
        )
      }
    >
      <Content>
        <DealButton
          disabled={false}
          onClick={onActivateAll}
          color={colors['success']}
          loadingColor={colors['neutralDark']}
        >
          {cashbackLabel}
        </DealButton>
        <NoStyleButton onClick={onActivateDeal}>
          {isO2
            ? t('contentApp.deals.available.activateWithoutCashback')
            : t('contentApp.deals.available.cashback', { cashback })}
        </NoStyleButton>
      </Content>
    </Popup>
  );
};

export default DealsAvailable;
