import React, { useMemo } from 'react';
import { useTranslation } from 'react-i18next';
import styled from 'styled-components';
import { DealActivatingMessage } from '../../common/messageTypes/contentScriptMessage';
import { colors } from '../../common/theming';
import { DealButton, Popup } from '../components';
import { useTimeout } from './helpers';
import { BaseMessagePopupProps } from './types';

const Content = styled.div`
  width: 100%;
`;

const DealActivating: React.FC<
  BaseMessagePopupProps & DealActivatingMessage
> = ({ deal, isO2, ...props }) => {
  const { t } = useTranslation();

  const label = useMemo(() => {
    return deal.cashback > 0 && !isO2
      ? t('contentApp.deals.activating.cashback')
      : t('contentApp.deals.activating.basic');
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [deal.cashback]);

  const onDismiss = () => {
    if (props.onClose) {
      props.onClose();
    }
  };

  useTimeout(2000, onDismiss);

  return (
    <Popup {...props} showClose={false} showLogo={false}>
      <Content>
        <DealButton
          disabled={true}
          loading={true}
          loadingColor={colors['neutralDark']}
          className="outlined"
        >
          {label}
        </DealButton>
      </Content>
    </Popup>
  );
};

export default DealActivating;
