import React from 'react';
import { useTranslation } from 'react-i18next';
import styled from 'styled-components';
import { useCoupons } from '../../common/deal';
import { DealCheckoutPageMessage } from '../../common/messageTypes/contentScriptMessage';
import { Popup } from '../components';
import CouponCard from './CouponCard';
import { BaseMessagePopupProps } from './types';

const Container = styled.div`
  display: flex;
  position: relative;
  flex-direction: row;
`;

const DealCheckoutPage: React.FC<
  BaseMessagePopupProps & DealCheckoutPageMessage
> = ({ deal, isO2, ...props }) => {
  const { t } = useTranslation();
  const { hasCoupons, codes } = useCoupons(deal);

  const handleClose = () => {
    if (props.onClose) {
      props.onClose();
    }

    browser.runtime.sendMessage({
      type: 'closeCheckoutPopup',
    });
  };

  if (!hasCoupons) return null;

  return (
    <Popup
      {...props}
      title={t('contentApp.deals.checkout.titleWithCode')}
      onClose={handleClose}
    >
      <Container>
        {codes.map((coupon) => (
          <CouponCard {...coupon} key={coupon.code} />
        ))}
      </Container>
    </Popup>
  );
};

export default DealCheckoutPage;
