import clsx from 'clsx';
import React, { useState } from 'react';
import { useTranslation } from 'react-i18next';
import styled from 'styled-components';
import { CouponCode } from '../../api/types';
import { writeToClipboard } from '../../common/clipboard';
import { Card, UnstyledButton } from '../../common/components';
import { themeColor } from '../../common/theming';

const CouponCodeContainer = styled(Card)`
  display: grid;
  grid-template-columns: 1fr 42px;
  padding: 10px;
  color: ${themeColor('neutralDark')};
  font-size: 12px;
  width: 200px;

  & > div > p {
    color: ${themeColor('neutral')};
    font-size: 11px;
    margin: 0;
    margin-top: 5px;
    padding: 0;
    width: 140px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
`;

const ClipboardSpan = styled(UnstyledButton)`
  grid-area: 1 / 2 / 2 / 3;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  width: 100%;
  height: 100%;
  color: ${themeColor('neutral')};
  &.copied {
    color: ${themeColor('success')};
  }
`;

const CouponCard: React.FC<CouponCode> = (coupon) => {
  const { t } = useTranslation();
  const [copied, setCopied] = useState(false);

  const onCopy = () => {
    writeToClipboard(coupon.code ?? '');
    setCopied(true);
  };

  return (
    <CouponCodeContainer key={coupon.code}>
      <div>
        <b>{coupon.code}</b>
        <p title={coupon.summary}>{coupon.summary}</p>
      </div>
      <ClipboardSpan onClick={onCopy} className={clsx({ copied })}>
        {copied
          ? t('contentApp.deals.checkout.copied')
          : t('contentApp.deals.checkout.copy')}
      </ClipboardSpan>
    </CouponCodeContainer>
  );
};

export default CouponCard;
