import React from 'react';
import { ThemeProvider as StyledThemeProvider, css } from 'styled-components';
import colors from './colors';

export interface Theme {
  colors: typeof colors;
}

declare module 'styled-components' {
  // eslint-disable-next-line @typescript-eslint/no-empty-interface
  interface DefaultTheme extends Theme {}
}

interface ThemeProps {
  theme: Theme;
}

type ColorName = keyof typeof colors;

const themeColor =
  (color: ColorName) =>
  ({ theme }: ThemeProps) =>
    theme.colors[color];

interface BoxShadowProps {
  offset: {
    x: number;
    y: number;
  };
  blur: number;
  opacity: number;
  spread: number;
  color: string;
}

const boxShadow = ({
  offset = { x: 3, y: 3 },
  blur = 7,
  opacity = 0.25,
  spread = 0,
  color,
}: Partial<BoxShadowProps> = {}) => {
  return `${offset.x}px ${offset.y}px ${blur}px ${spread}px ${
    color ?? `rgba(0, 0, 0, ${opacity})`
  }`;
};

const rootStyles = css`
  font-family: Fira Sans, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto,
    Oxygen, Ubuntu, Cantarell, Droid Sans, Helvetica Neue, sans-serif;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;
  line-height: normal;
  & * {
    font-family: inherit;
    box-sizing: border-box;
  }
  & button,
  & a {
    cursor: pointer;
  }
`;

const ThemeProvider: React.FC = ({ children }) => (
  <StyledThemeProvider theme={{ colors }}>{children}</StyledThemeProvider>
);

export { ThemeProvider, colors, themeColor, boxShadow, rootStyles };
