import React from 'react';
import styled from 'styled-components';
import { clamp } from '../numbers';

const LeafImg = styled.img`
  height: 17px;
`;

interface Props {
  score: number;
}
const MIN_SCORE = 0;
const MAX_SCORE = 5;

const EarthmarkScore: React.FC<Props> = (props) => {
  const score = clamp(props.score, MIN_SCORE, MAX_SCORE);

  return (
    <div>
      {[...Array(score)].map((n) => (
        <LeafImg
          key={`active-${n}`}
          src={require('../../assets/images/active-leaf.png')}
        />
      ))}
      {[...Array(MAX_SCORE - score)].map((n) => (
        <LeafImg
          key={`active-${n}`}
          src={require('../../assets/images/inactive-leaf.png')}
        />
      ))}
    </div>
  );
};

export default EarthmarkScore;
