import clsx from 'clsx';
import React from 'react';
import { useTranslation } from 'react-i18next';
import styled from 'styled-components';
import { themeColor } from '../../common/theming';
import { CloseButton, Popup } from '../components';
import { useTimeout } from './helpers';
import { MessagePopupsProps } from './types';

const Content = styled.div`
  width: 100%;

  &.inline {
    display: grid;
    grid-template-columns: auto 30px;
  }
`;

const Text = styled.p`
  margin: 0;
  color: ${themeColor('neutralDark')};
  width: 100%;
`;

const DealActivationFailed: React.FC<MessagePopupsProps> = ({
  isO2,
  ...props
}) => {
  const { t } = useTranslation();

  const onDismiss = () => {
    if (props.onClose) {
      props.onClose();
    }
  };

  useTimeout(2000, onDismiss);

  return (
    <Popup
      {...props}
      title={!isO2 ? t('contentApp.deals.failed.title') : undefined}
      showLogo={!isO2}
      showClose={!isO2}
    >
      <Content
        className={clsx({
          inline: isO2,
        })}
      >
        <div>
          {isO2 && (
            <Text>
              <b>{t('contentApp.deals.failed.title')}</b>
            </Text>
          )}
          <Text>{t('contentApp.deals.failed.reported')}</Text>
        </div>
        {isO2 && <CloseButton onClick={onDismiss} />}
      </Content>
    </Popup>
  );
};

export default DealActivationFailed;
