import { useMemo } from 'react';
import { CashbackType, DealSummaryWithDomains } from '../api/types';
import { formatCashback, formatMoney } from './money';

export const useCashback = (deal: DealSummaryWithDomains) =>
  useMemo(() => {
    if (deal.cashbackType === CashbackType.fixed) {
      return formatMoney(deal.currency ?? 'GBP', deal.cashback);
    }
    return formatCashback(deal.cashback);
  }, [deal]);

export const useCoupons = (deal: DealSummaryWithDomains) =>
  useMemo(() => {
    const codes = Array.from(
      new Map(
        deal.codes
          .filter((c) => Boolean(c.code))
          .map((item) => [item.code, item])
      ).values()
    );

    return {
      hasCoupons: codes.length > 0,
      codes,
    };
  }, [deal]);

export const useEarthmarkScore = (deal: DealSummaryWithDomains) =>
  useMemo(() => {
    if (
      deal.earthmarkRating !== undefined &&
      deal.earthmarkRating !== null &&
      deal.earthmarkRating > 0
    ) {
      return deal.earthmarkRating;
    } else {
      return null;
    }
  }, [deal.earthmarkRating]);
