import React from 'react';
import styled from 'styled-components';
import env from '../../env';

const Logo = styled.img`
  width: 16px;
  height: 16px;
  grid-area: 1 / 1 / 2 / 2;
  align-self: center;
`;

const PopupLogo: React.FC = () => {
  return (
    <Logo
      src={
        env.REACT_APP_SDK_MODE
          ? browser.runtime.getURL('icons/color/logo-alt.png')
          : browser.runtime.getURL('icons/color/logo.png')
      }
    />
  );
};

export default PopupLogo;
