import React from 'react';
import { ContentScriptMessage } from '../common/messageTypes/contentScriptMessage';
import BalanceChanged from './popups/BalanceChanged';
import DealActivated from './popups/DealActivated';
import DealActivating from './popups/DealActivating';
import DealActivation from './popups/DealActivation';
import DealActivationFailed from './popups/DealActivationFailed';
import DealCheckoutPage from './popups/DealCheckoutPage';
import DealsAvailable from './popups/DealsAvailable';

interface Props {
  message: ContentScriptMessage;
  isO2: boolean;
  onClose?(): void;
}

const MessageContent: React.FC<Props> = ({ message, ...props }) => {
  const render = () => {
    switch (message.type) {
      case 'dealsAvailable':
        return <DealsAvailable {...message} {...props} />;
      case 'dealActivating':
        return <DealActivating {...message} {...props} />;
      case 'dealActivated':
        return <DealActivated {...message} {...props} />;
      case 'dealActivationFailed':
        return <DealActivationFailed {...message} {...props} />;
      case 'dealCheckoutPage':
        return <DealCheckoutPage {...message} {...props} />;
      case 'balanceChanged':
        return <BalanceChanged {...message} {...props} />;
      default:
        return null;
    }
  };

  return render();
};

export default MessageContent;
